.Demo {
  padding: 2rem;
  max-width: 600px;
  margin: 0 auto;
}

.Demo form {
  display: flex;
  flex-direction: column;
}

.Demo input, .Demo textarea {
  margin-bottom: 1rem;
  padding: 0.5rem;
}

.Demo button {
  background-color: #ff6b6b;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.Demo pre {
  background-color: #f0f0f0;
  padding: 1rem;
  border-radius: 5px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
