.App {
  font-family: Arial, sans-serif;
  color: #333;
}

.logo {
  display: flex;
  align-items: center;
}



.logo-image {
  max-width: 150px; /* Adjust this value to set the maximum width of your logo */
  height: auto;
  width: 100%;
  display: block;
}

@media (max-width: 768px) {
  .logo-image {
    max-width: 100px; /* Smaller size for mobile devices */
  }
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #fff;
}

nav a {
  margin-right: 1rem;
  text-decoration: none;
  color: #333;
}

.cta-button {
  background-color: #ff6b6b;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
}

.hero {
  text-align: center;
  padding: 4rem 2rem;
  background: linear-gradient(to bottom right, #fff5f5, #fff0f0);
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}


.cta-buttons {
  margin-top: 2rem;
}

.cta-buttons button {
  margin: 0 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.primary-cta {
  background-color: #ff6b6b;
  color: white;
}

.secondary-cta {
  background-color: white;
  color: #ff6b6b;
  border: 1px solid #ff6b6b;
}

.technologies, .features, .testimonials, .faq {
  padding: 4rem 2rem;
  text-align: center;
}

.tech-icons, .feature-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.tech-icon, .feature-card {
  margin: 1rem;
  padding: 1rem;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.testimonials {
  background-color: #2d3748;
  color: white;
}

.testimonial-card {
  background-color: #4a5568;
  padding: 2rem;
  border-radius: 5px;
  margin: 1rem;
}

footer {
  background-color: #2d3748;
  color: white;
  padding: 2rem;
  text-align: center;
}

footer a {
  color: white;
  margin: 0 0.5rem;
  text-decoration: none;
}

.demo-video img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  margin-top: 2rem;
}

.faq-item {
  text-align: left;
  margin-bottom: 2rem;
}

.faq-item h3 {
  margin-bottom: 0.5rem;
}
.Demo {
  padding: 2rem;
  max-width: 600px;
  margin: 0 auto;
}

.Demo form {
  display: flex;
  flex-direction: column;
}

.Demo input, .Demo textarea {
  margin-bottom: 1rem;
  padding: 0.5rem;
}

.Demo button {
  background-color: #ff6b6b;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.Demo pre {
  background-color: #f0f0f0;
  padding: 1rem;
  border-radius: 5px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
