body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 10px;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin: 20px 0;
}

input, textarea {
  margin-bottom: 10px;
  padding: 5px;
}

button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}



